import React from "react";
import { Story, Meta } from "@storybook/react/types-6-0";

import Avatar from "../../components/Avatar";
import {
  AvatarProps,
  defaultAvatarProps as avatarDefaultProps,
  avatarSize,
  AvatarSizeType,
} from "../../components/Avatar/Avatar.types";

export default {
  title: "Atom/Avatar",
  component: Avatar,
} as Meta;

const Template: Story<AvatarProps> = (props) => <Avatar {...props} />;
const List: Story<AvatarProps> = (props) => {
  return (
    <React.Fragment>
      {Object.keys(avatarSize).map((value) => (
        <Avatar key={value} size={value as AvatarSizeType} {...props} />
      ))}
    </React.Fragment>
  );
};
export const WithImage = Template.bind({});;
export const WithoutImage = Template.bind({});;
export const onClick = Template.bind({});;
export const AvatarList = List.bind({});

WithImage.args = {
  ...avatarDefaultProps,
  src: "https://images.unsplash.com/photo-1511485977113-f34c92461ad9?ixlib=rb-1.2.1&w=128&h=128&dpr=2&q=80",
};

WithoutImage.args = {
  ...avatarDefaultProps,
};

onClick.args = {
  ...avatarDefaultProps,
  onClick: () => alert("Image onClick"),
};



WithImage.parameters = { storySource: { source: "(props) => <Avatar {...props} />" }, ...WithImage.parameters };
WithoutImage.parameters = { storySource: { source: "(props) => <Avatar {...props} />" }, ...WithoutImage.parameters };
